@use '../base/mixins' as *;
@use '../components';

.dark-theme {
  color-scheme: dark;

  // ------ Skeleton mixin, dark scheme ------ //
  $dark-skeleton-color: #343538;
  .programs-list-wrapper-placeholder {
    .program-list-skeleton {
      @include skeleton-gradient($dark-skeleton-color);
    }
  }

  .basic-programs-table-placeholder {
    .animate-skeleton {
      @include skeleton-gradient($dark-skeleton-color);
    }
  }

  .programs-table-skeleton {
    .row-skeleton {
      div {
        @include skeleton-gradient($dark-skeleton-color);
      }
    }
  }

  .upload-image-placeholder {
    .upload-image-skeleton {
      @include skeleton-gradient($dark-skeleton-color);
    }
  }

  .exercise-card-skeleton {
    @include skeleton-gradient($dark-skeleton-color);
  }

  .background-wrapper-placeholder {
    .main-wrap-placeholder {
      @include skeleton-gradient($dark-skeleton-color);
    }
  }
  // ------ ------ ------ ------ //

  $dark-theme: #121317;
  .fetchingDataSpinner {
    @include adjust-color($dark-theme);
  }
}
